@media screen and (max-width: 900px) {
    .bg-hcombs,
    .bgFrst,
    .bgScnd,
    .bgFrd,
    .bgFft,
    .bg-dark {
        height: 100%;
    }
    .loaderBgColor {
        top: 40vh;
        left: calc(50% - calc(450px * 0.8));
        height: calc(400px * 0.8);
        width: calc(900px * 0.8);
        background: rgb(0, 136, 136);
    }
    .loaderBgImg {
        top: 40vh;
        left: calc(50% - calc(450px * 0.8));
        height: calc(400px * 0.8);
        width: calc(900px * 0.8);
    }
    .loaderBgRed {
        top: 40vh;
        left: calc(50% - calc(450px * 0.8));
        height: calc(400px * 0.8);
    }
    @keyframes loaderColor {
        0% {width: calc(150px * 0.8);}
        100% {width: calc(900px * 0.8);}
    }   
    h1 {
        text-align: center;
        width: 90%;
        padding: 0 0 10px 0;
        margin: 30vh auto 0;
        border-left: none;
        border-bottom: 2px solid cyan;
        font-size: 28px;
    }
    h2 {
        text-align: center;
        margin: 40px auto;
        padding: 0;
        width: 90%;
        font-size: 20px;
    }
    .header-content {
        margin: 0;
    }
    .mainMenu {
        a,p {
            font-size: 18px;
        }
    }
    .menuLogoOpen {
        width: 40px;
        height: 40px;
    }
    .mainMenuPrtRight {
        width: 450px;
    }
}



