
.loader {
    display: block;
    height: 100vh;
    width: 100%;
    position: fixed;
    z-index: 666;
    background: black;
    animation: 4s ease-in-out both loader;
}
@keyframes loader {
    0% {background: rgb(0, 0, 71);}
    100% {background: brown;}
}
.loaderBg {
    display: block;
    height: 100vh;
    width: 100%;
    background: fixed center / cover no-repeat url('../img/loaderBkgCmp.png');
}
.loaderBgColor {
    display: block;
    position: absolute;
    top: 10vh;
    left: calc(50% - 450px);
    height: 400px;
    width: 900px;
    background: rgb(0, 136, 136);
}
.loaderBgImg {
    position: absolute;
    top: 10vh;
    left: calc(50% - 450px);
    display: block;
    height: 400px;
    width: 900px;
    background-color: black;
    background: center / cover no-repeat url('../img/Scheme.png');
}
.loaderBgRed {
    display: block;
    position: absolute;
    top: 10vh;
    left: calc(50% - 450px);
    height: 400px;
    background: brown;
    animation: 4s ease-in-out both loaderColor;
}
@keyframes loaderColor {
    0% {width: 150px;}
    100% {width: 900px;}
}
body {
    background-color: rgba(0, 0, 0, 0.162);
    overflow-x: hidden;
}
a {
    text-decoration: none;
    color: black;
    font-size: 25px;
}
section {
    display: block;
    margin: 0 auto;
    width: 100%;
}
.container {
    display: block;
    margin: 0 auto;
    width: 1200px;
}
.bg-hcombs {
    width: 100%;
    height: 100vh;
    background: fixed center / contain repeat url('../img/MainWallpaper1.png');
    z-index: 3;
    top: 0;
    left: 0;
    position: absolute;
}
.bgFrst {
    position: absolute;
    display: block; 
    width: 25%;
    height: 100vh;
    animation: 7s 2s ease-in reverse none infinite gradientRed;
    top: 0;
    left: 0;
}
.bgScnd {
    position: absolute;
    width: 25%;
    height: 100vh;
    animation: 7s 2.1s ease-in reverse none infinite gradientRed;
    top: 0;
    left: 25%;
}
.bgFrd {
    position: absolute;
    width: 25%;
    height: 100vh;
    animation: 7s 2.2s ease-in reverse none infinite gradientRed;
    top: 0;
    left: 50%;
}
.bgFft {
    position: absolute;
    width: 25%;
    height: 100vh;
    animation: 7s 2.3s ease-in reverse none infinite gradientRed;
    top: 0;
    left: 75%;
}
.bg-dark {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    position: absolute;
    top: 0;
    left: 0;
    z-index: 5;
}
@keyframes gradientBlue {
    0% {background: blue;}
    25% {background: cornflowerblue;}
    50% {background: cyan;}
    75% {background: violet;}
    100% {background: blue;}
}
@keyframes gradientRed {
    9% {background: red;}
    18% {background: purple;}
    27% {background: blueviolet;}
    36% {background: blue;}
    45% {background: cornflowerblue;}
    54% {background: cyan;}
    63% {background: violet;}
    72% {background: blueviolet;}
    81% {background: purple;}
    90% {background: rgb(159, 0, 0);}
    99% {background: red;}
}

.menuLogoOpen {
    display: block;
    width: 50px;
    height: 50px;
    background: center no-repeat / 70% url(../img/menu.png);
    cursor: pointer;
    transition-duration: 0.2s;
    &:hover {
        transform: scale(1.1);
        border: 1px solid gray;
        border-radius: 5px;
    }
}
.mainMenu {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    width: 100%;
    height: 60px;
    background: white;
    z-index: 9;
    // &:hover {
    //     background: rgba(255, 255, 255, 0.7);
    // }
}

.mainMenuPrtLeft {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
    width: 150px;
}
.mainMenuPrtRight {
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    width: 550px;
}
.mainMenuIcon {
    display: block;
    width: 50px;
    height: 50px;
    background: center no-repeat / contain url(../img/logo-icon-compr.png);
    cursor: pointer;
    transition-duration: 0.2s;
    &:hover {
        transform: scale(1.1);
        border: 1px solid gray;
        border-radius: 50%;
    }
}
.translate {
    display: block;
    width: 50px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    cursor: pointer;
    transition-duration: 0.2s;
    &:hover {
        transform: scale(1.1);
        border: 1px solid gray;
        border-radius: 50%;
    }
}
.mainNumber,
.mainMail {
    transition-duration: 0.2s;
    line-height: 50px;
    text-align: center;
    &:hover {
        transform: scale(1.1);
        border: 1px solid gray;
        border-radius: 5px;
    }
}


section {
    height: 200vh;
}

.header-content {
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    z-index: 5;
    width: 100%;
    height: 100%;
}
h1 {
    margin: 130px 0 0 20px;
    padding: 0 20px;
    border-left: 2px solid cyan;
    font-size: 40px;
    width: 800px;
    color: white;
    cursor: default;
    animation: 1.8s 2.6s ease-in none MoveLeft;
}
@keyframes MoveLeft {
    0% {transform: translateX(-1500px);}
    100% {transform: translateX(0);}
}
h2 {
    margin: 40px 0 0 10px;
    padding: 0 10px;
    font-size: 22px;
    width: 800px;
    color: white;
    cursor: default;
    animation: 2s 2.6s ease-in none MoveLeft;
}